.profile__info {
    display: flex;
    justify-content: space-around;
    border: 1px solid #E5E5E5;
    margin-bottom: 20px;
  background-color: white;
  }
  .profile__info__avatar {
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 100%;
  }
  
  .profile__background {
    width: 100%;
    height: 500px;
  }

.input__wrapper {
  width: 100%;
  position: relative;
  margin: 15px 0;
  text-align: center;
}

.input__file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.input__file-icon-wrapper {
  height: 60px;
  width: 60px;
  margin-right: 15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  border-right: 1px solid #fff;
}

.input__file-button-text {
  line-height: 1;
  margin-top: 1px;
}

.input__file-button {
  width: 100%;
  max-width: 200px;
  height: 35px;
  background: #7573fb;
  color: #fff;
  font-size: 0.9rem;
  font-weight: 700;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  border-radius: 3px;
  cursor: pointer;
  margin: 0 auto;
}
.profile_info__item-block {

}

.profile_info__item {
  display: flex;
  align-content: center;
  align-items: center;
  gap: 5px;
  margin: 0;
}
.profile_info__item p {
  font-size: 11px;
  margin: 0;
  padding: 0;
}